    margin: 0;
    padding: 40px;
    width: 820px;
    max-height: 90vh;
    overflow-y: auto;
    position: relative;
    animation: slideUp 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .modal-content-inner {
    height: 100%;
    max-height: calc(90vh - 80px);
    overflow-y: auto;
    padding-right: 8px;
    margin-right: -8px;
  }
  
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  @keyframes slideUp {
    from { 
      opacity: 0;
      transform: translateY(40px) scale(0.95);
    }
    to { 
      opacity: 1;
      transform: translateY(0) scale(1);
    }
  }
  
  .modal-content h2 {
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
