/* 進捗表示用のCSS - LaserCutImageProcessor.cssに追加 */

.processing-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  backdrop-filter: blur(8px);
  animation: fadeInOverlay 0.3s ease-out forwards;
}

@keyframes fadeInOverlay {
  from { opacity: 0; }
  to { opacity: 1; }
}

.processing-modal {
  background: linear-gradient(160deg, #1a1a1a, #0d0d0d);
  border-radius: 25px;
  padding: 45px;
  max-width: 550px;
  width: 90%;
  box-shadow: 0 15px 40px rgba(0, 255, 255, 0.2), 0 0 60px rgba(0, 255, 255, 0.1);
  border: 1.5px solid #00ffff;
  text-align: center;
  color: #e0e0e0;
  animation: scaleInModal 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) forwards;
}

@keyframes scaleInModal {
  from { transform: scale(0.8); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

.processing-content h3 {
  color: #ffffff;
  margin-bottom: 35px;
  font-size: 1.8rem;
  font-weight: 700;
  text-shadow: 0 0 8px rgba(0, 255, 255, 0.3);
}

.processing-spinner {
  width: 70px;
  height: 70px;
  border: 5px solid rgba(0, 255, 255, 0.2);
  border-top: 5px solid #00ffff;
  border-radius: 50%;
  animation: spin 1.2s cubic-bezier(0.68, -0.55, 0.27, 1.55) infinite;
  margin: 0 auto 25px;
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.4);
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.progress-bar-container {
  margin: 25px 0;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  position: relative;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3);
}

.progress-fill {
  height: 100%;
  background: #00ffff;
  border-radius: 4px;
  transition: width 0.3s ease;
  position: relative;
  box-shadow: 0 0 8px rgba(0, 255, 255, 0.4);
}

.progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  animation: shine 2s infinite ease-in-out;
}

@keyframes shine {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.progress-text {
  margin-top: 20px;
  color: #00ffff;
  font-weight: 700;
  font-size: 1.3rem;
  text-shadow: 0 0 6px rgba(0, 255, 255, 0.3);
}

.processing-message {
  margin-top: 20px;
  color: #b0b0b0;
  font-size: 1rem;
  min-height: 25px;
  line-height: 1.4;
}

.processing-tips {
  margin-top: 40px;
  text-align: left;
  background: rgba(0, 0, 0, 0.3);
  padding: 25px;
  border-radius: 12px;
  border: 1.5px solid rgba(255, 255, 255, 0.15);
  opacity: 0;
  animation: fadeInContainer 0.8s ease 0.3s forwards;
}

@keyframes fadeInContainer {
  to {
    opacity: 1;
  }
}

.processing-tips h4 {
  margin: 0 0 20px 0;
  color: #00ffff;
  font-weight: 600;
  font-size: 1.1rem;
  opacity: 0;
  animation: fadeInTitle 0.6s ease forwards;
}

@keyframes fadeInTitle {
  to {
    opacity: 1;
  }
}

.tips-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.tip-item {
  color: #d0d0d0;
  font-size: 0.9rem;
  line-height: 1.6;
  padding: 8px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  opacity: 0;
  transform: translateY(10px);
  animation: fadeInUp 0.5s ease forwards;
}

.tip-item:nth-child(1) { animation-delay: 0.5s; }
.tip-item:nth-child(2) { animation-delay: 0.7s; }
.tip-item:nth-child(3) { animation-delay: 0.9s; }

.tip-item:last-child {
  border-bottom: none;
}

.tip-item::before {
  content: '•';
  color: #ffffff;
  font-weight: bold;
  margin-right: 12px;
  opacity: 0.7;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { 
    opacity: 0.7;
    transform: scale(1);
  }
  50% { 
    opacity: 1;
    transform: scale(1.2);
  }
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

